import { Typography, Chip } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import React from 'react'

const useStyles = makeStyles(theme => ({
  root: {
    marginBottom: theme.spacing(1)
  },
  listUnstyled: {
    listStyleType: 'none',
    marginTop: 0,
    lineHeight: '1.75rem'
  },
  listStyled: {
    listStyleType: 'circle',
    marginTop: 0,
    lineHeight: '1.75rem'
  },
  stepStyle: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    textAlign: 'justify'
  },
  image: {
    width: '100%',
    borderRadius: '1.5rem',
    display: 'block',
    marginBottom: theme.spacing(1)
  },
  chip: {
    marginRight: theme.spacing(1),
    marginBottom: theme.spacing(1),
    marginTop: theme.spacing(1)
  }
}))

export default props => {
  const { cookTime, ration, materials, steps, level, categories } = props

  const classes = useStyles()

  return (
    <>
      <Typography variant="body1">
        <strong>Thi gian thc hin:</strong> {cookTime} pht
      </Typography>
      {ration && (
        <Typography variant="body1">
          <strong>Khu phn:</strong> {ration} ngi
        </Typography>
      )}
      <Typography variant="body1">
        <strong> kh:</strong>{' '}
        {level === 1 ? 'D' : level === 2 ? 'Trung bnh' : 'Kh'}
      </Typography>
      {categories && categories.length > 0 && categories[0] !== '' && (
        <div className="materials" style={{ marginTop: 16 }}>
          <Typography variant="h6" color="primary">
            Nhm thc n
          </Typography>
          {categories.map((item, index) => (
            <Chip
              key={`chip${index}`}
              label={item}
              color={'primary'}
              className={classes.chip}
              onClick={() => {}}
            />
          ))}
        </div>
      )}
      {materials && materials.length > 0 && (
        <div className="materials">
          <Typography variant="h6" color="primary">
            Nguyn liu
          </Typography>
          <ul className={classes.listUnstyled}>
            {materials.map(material => (
              <li key={material}>{material}</li>
            ))}
          </ul>
        </div>
      )}
      {steps && steps.length > 0 && (
        <div className="steps">
          <Typography variant="h6" color="primary">
            Cc bc thc hin
          </Typography>
          {steps.map((step, index) => (
            <div className="step" key={`step${index}`}>
              <Typography variant="body1" className={classes.stepStyle}>
                <strong>Bc {step.stt}:</strong> {step.making}
              </Typography>
              {step.image && (
                <img src={step.image} alt="img" className={classes.image} />
              )}
            </div>
          ))}
        </div>
      )}
    </>
  )
}
