import { makeStyles } from '@material-ui/core/styles'
import { COLOR } from 'ultis/functions'
import * as yup from 'yup'

export const IMAGE_TYPE = {
  NORMAL: 'NORMAL',
  WIDE: 'WIDE'
}

export const CATEGORY_ITEMS = [
  'Mn Vit',
  'Mn Trung',
  'Mn Hn',
  'Mn Nht',
  'Mn Thi',
  'Mn ',
  'Mn Php',
  ' ung',
  'Trng ming'
]

export const recipeStyles = makeStyles(theme => ({
  root: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2)
  },
  thumbnail: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(3),
    width: '100%',
    borderRadius: '1rem'
  },
  group: {
    marginBottom: theme.spacing(2)
  },
  field: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1)
  },
  paper: {
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center'
  },
  iconButton: {
    padding: 10
  },
  add: {
    marginRight: theme.spacing(2)
  },
  stepNum: {
    width: '30px',
    height: '30px',
    marginRight: theme.spacing(1),
    backgroundColor: COLOR.primary
  },
  button: {
    borderRadius: 25
  },
  errorStyle: {
    fontSize: '0.8rem',
    color: 'red'
  },
  chip: {
    marginRight: theme.spacing(1),
    marginTop: theme.spacing(1)
  },
  select: {
    marginTop: theme.spacing(1),
    width: '100%'
  }
}))

export const stepsSchema = yup.object({
  stt: yup.number(),
  making: yup.string().trim().required('* Vui lng nhp bc thc hin')
})

export const validationRecipeSchema = yup.object().shape({
  title: yup
    .string()
    .trim()
    .required('* Vui lng nhp tiu ')
    .max(255, 'Tiu  khng c qu 255 k t'),
  ration: yup.string().trim().required('* Vui lng nhp khu phn'),
  cookingTime: yup
    .number()
    .required('* Vui lng nhp thi gian thc hin')
    .min(1, 'Thi gian thc hin nhiu hn 0 pht'),
  difficultLevel: yup.number().required('* Vui lng chn  kh'),
  ingredients: yup
    .array()
    .required('* Vui lng thm t nht 1 nguyn liu')
    .of(yup.string().required('* Vui lng nhp nguyn liu')),
  steps: yup
    .array()
    .required('* Vui lng thm t nht 1 bc thc hin')
    .of(stepsSchema),
  avatar: yup
    .string()
    .nullable()
    .required('* Vui chn hnh i din cho cng thc')
})
